// 模拟当前登录用户的id
let currentUser_id = 1;
import $T from '@/common/time.js'
export default {
	props:{
		item: Object,
		index: Number,
		pre_time: [Number,String]
	},
	computed:{
		//判断是不是当前用户
		isSelf(){
			return currentUser_id === this.item.user_id
		},
		// 300s内只显示一条时间戳
		shortTime(){
			return $T.getChatTime(this.item.create_time,this.pre_time)
		}
	}
}
